/* montserrat-regular - latin_cyrillic */
@font-face {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    src: url("../fonts/montserrat-v25-latin_cyrillic-regular.woff2")
            format("woff2"),
        url("../fonts/montserrat-v25-latin_cyrillic-regular.woff")
            format("woff");
}

